.page-footer {
  text-align: center;
  padding: 36px 0 30px;
  max-width: 1200px;
  margin: 0 auto;
}

.page-footer--container {
  @media (min-width: 768px) {
    padding: 0 60px;
  }
}

.page-footer--photo {
  padding: 31px 0 30px;

  @media (min-width: 768px) {
    padding: 34px 60px 35px;
  }
}

.page-footer--form {
  padding: 74px 0 30px;
}


@media (min-width: 768px) {
  .page-footer--index {
    padding: 39px 60px;
  }

  .page-footer--form {
    padding: 95px 60px 35px;

    @media (min-width: 1200px) {
      padding: 100px 120px 35px;
      max-width: 1200px;
      margin: 0 auto;
    }
  }
}

@media (min-width: 1200px) {
  .page-footer--index {
    padding: 36px 120px;
  }

  .page-footer--photo {
    padding: 123px 121px 35px;
  }
}


.page-footer__wrapper {
  width: 150px;
  margin: 0 auto;

  @media (min-width: 768px) {
    width: 100%;
    display: grid;
    grid-template-columns: auto auto auto;
    justify-content: space-between;
    align-items: center;
  }
}

.page-footer__hashtag {
  font-size: 21px;
  line-height: 26px;
  font-weight: 700;
  margin-bottom: 35px;

  @media (min-width: 768px) {
    margin: 0;
  }
}

.page-footer__social-list {
  margin-bottom: 39px;
  width: 150px;
  height: 48px;

  @media (min-width: 768px) {
    margin: 0;
  }
}

.page-footer__copyright {
  width: 128px;
  margin: 0 auto;

  @media (min-width: 768px) {
    margin-left: 9px;
  }
}
